.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  width: 100%;
}

.wrapper {
  display: inline-grid;
  grid-template-rows: repeat(4, 6rem);
  grid-template-columns: repeat(4, 1fr);
  padding: 1rem;
}

.display {
  height: 50%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  border: 1px solid red;
  padding: 2rem;
}
